<template>
    <div class="carousel-box d-lg-block d-md-block d-sm-block d-none" style="background-image:url('./static/images/bg.jpg');">
		<div class="container">
			<div class="row justify-content-center">
				<div class="col-md-8 col-sm-12">
					<div id="carousel" class="carousel slide" data-ride="caroudel">
						<ol class="carousel-indicators">
							<li data-target="#carousel"
								v-for="(caoruDate,i) in caourselData"
								v-bind:data-slide-to="i"
								v-bind:class="i==0?'active':''"></li>
						</ol>

						<div class="carousel-inner">
							<div class="carousel-item" v-for="(caoruDate,i) in caourselData" v-bind:class="i==0?'active':''">
								<a class="d-block w-100" v-bind:href="caoruDate.href" target="_blank">
									<img v-bind:src="caoruDate.img" alt="" class="img-fluid">
								</a>
							</div>
						</div>
						<a class="carousel-control-prev" href="#carousel" role="button" data-slide="prev">
							<span class="carousel-control-prev-icon" aria-hidden="true"></span>
							<span class="sr-only">Previous</span>
						</a>
						<a class="carousel-control-next" href="#carousel" role="button" data-slide="next">
							<span class="carousel-control-next-icon" aria-hidden="true"></span>
							<span class="sr-only">Next</span>
						</a>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var caourselData = [
  {
    href: "https://travel-website.herokuapp.com/",
    img: "./static/images/carousel/20180427.jpg",
    title: "旅遊網",
    type: "Node Express MongoDB RWD"
  },
  {
    href: "http://psycho909.github.io/mywebsite/web/171011_bb/index.html",
    img: "./static/images/carousel/171011_bb.jpg",
    title: "全民打棒球",
    type: "HTML"
  },
  {
    href: "http://psycho909.github.io/mywebsite/web/170929_sw/index.html",
    img: "./static/images/carousel/170929_sw.jpg",
    title: "靈魂行者",
    type: "HTML"
  },
  {
    href: "http://psycho909.github.io/mywebsite/web/170912_cd/index.html",
    img: "./static/images/carousel/170912_cd.jpg",
    title: "狂魔蓋台",
    type: "HTML,Vue"
  },
  {
    href: "http://psycho909.github.io/mywebsite/web/170804_sw/index.html",
    img: "./static/images/carousel/170804_sw.jpg",
    title: "靈魂行者",
    type: "HTML"
  },
  {
    href: "http://cd.wasabii.com.tw/index.aspx",
    img: "./static/images/carousel/cd.jpg"
  },
  {
    href: "http://psycho909.github.io/mywebsite/web/170614_cd/",
    img: "./static/images/carousel/cd01.jpg"
  }
];
export default {
  name: "Carousel",
  data() {
    return {
      caourselData: caourselData,
      bg: "./static/images/bg.jpg"
    };
  },
  methods: {
    get_style() {
      return {
        "background-image": url("./static/images/bg.jpg")
      };
    }
  }
};
</script>
<style>

</style>
<style lang="scss" scope>
$yellow_color: #fff100;
.carousel-indicators {
  li.active {
    margin-right: 6px;
    background-color: $yellow_color;
    border: none;
  }
  li {
    margin-right: 6px;
  }
}
.carousel-control {
  top: 50%;
  opacity: 1;
  width: 36px;
  height: 36px;
  line-height: 36px;
  font-size: 36px;
  border-radius: 50%;
  text-shadow: none;
  color: $yellow_color;
  font-size: 64px;
  &:hover {
    color: $yellow_color;
    opacity: 1;
  }
  &:focus {
    color: $yellow_color;
    opacity: 1;
  }
  &.left {
    background-color: transparent;
    left: 0;
    transform: translate(-200%, -50%);
    background-image: none;
  }
  &.right {
    background-color: transparent;
    right: 0;
    transform: translate(200%, -50%);
    background-image: none;
  }
}

@media (min-width: 768px) {
  .carousel-box {
    width: 100%;
    background-position: center 80%;
    background-attachment: fixed;
    background-color: #393939;
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-image: url("../../static/images/black.png");
      opacity: 0.5;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}
</style>


