<template>
    <div class="container mt-3">
		<div class="card-columns">
			<div class="card" v-for="data in designData">
				<a data-fancybox="gallery" v-bind:href="data.img" class="card-img-top">
					<img v-bind:src="data.img" alt="" class="img-fluid w-100">
				</a>
			</div>
		</div>
	</div>
</template>
<script>
    export default{
        name:'Webdesign',
        data(){
            return{
                designData:[]
            }
        },
        mounted(){
			var _this=this;
        	var url='./static/js/design.data.js';
        	fetch(url,{method:'get'}).then((res)=>{
        		res.json().then(data=>{
        			_this.designData=data;
        		})
        	})
        }
    }
</script>
<style lang="scss" scope>


</style>


