<template>
    <nav class="user-navbar navbar navbar-expand-lg navbar-dark bg-dark">
        <router-link to="/" class="navbar-brand">作品集</router-link>
		<button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar">
			<span class="navbar-toggler-icon"></span>
		</button>
        <div class="collapse navbar-collapse mt-lg-0 mt-md-0 mt-sm-0 mt-3" id="navbar">
			<ul class="nav navbar-nav ml-auto">
				<li :class="$route.fullPath === '/'?'active nav-item':'nav-item'"><router-link to="/" class="nav-link">WebDesign</router-link></li>
				<li :class="$route.fullPath === '/Design'?'active nav-item':'nav-item'"><router-link to="/Design" class="nav-link">Design</router-link></li>
			</ul>
        </div>
    </nav>
</template>
<script>
    export default{
        name:'Navbar',
        data(){
        	return{
        		currentPath:''
        	}
        },
		route: {
			data: function(transition) {
			transition.next({
				currentPath: transition.to.path
			});
			}
		}
    }
</script>
<style lang="scss" scope>
$yellow_color:#FFF100;
.user-navbar{
	background-color: #333;
	border: 4px solid #fff;
	border-radius: 0;
	.navbar-header{
		.navbar-brand{
			color:#fff;
		}
	}
	.navbar-toggle{
		border: none;
		&:hover .icon-bar{
			background-color: $yellow_color;
		}
		&:focus{
			background-color: transparent;
		}
		.icon-bar{
			background-color: #fff;
		}
		&:hover{
			background-color: transparent;
		}
	}

	#navbar{
		.nav{
			li{
				&.active:hover a{
					color:#000;
				}
				&:hover a{
					color:$yellow_color;
				}
				a{
					color:#fff;
				}
			}
		}
	}
}
</style>


