<template>
    <div class="contact container">
        <div class="contact-inner" :class="$route.fullPath == '/Contact' ? 'acc':'' ">23</div>
        <div>{{msg}}</div>
    </div>
</template>
<script>
    export default{
        name:'Contact',
        data(){
            return{
                msg:'Hello This is Contact Page'
            }
        },
        mounted(){
            $('.contact-inner').removeClass('active')
            if(!$('.contact-inner').hasClass('acc')){
                
            }else{
                $('.contact-inner').addClass('active')
            }
        }
    }
</script>
<style lang="scss" scope>
    .contact{
        flex:1 0 auto;
    }
    .contact-inner.acc{
        opacity:0;
        transition:all 1s;
        &.active{
            opacity:1;
        }
    }
</style>


