<template>
    <div class="container mt-3">
		<div class="card-columns">
			<div class="card my-card" v-for="data in webdesignData">
				<img v-bind:src="data.img" alt="" class="img-fluid card-img-top">
				<div class="card-body my-card-body justify-content-start align-items-end">
					<div class="row">
						<div class="col-md-8 col-sm-8 col-12">{{data.title}}</div>
						<div class="col-md-4 col-sm-4 col-12">{{data.type}}</div>
						<div class="col-md-12">
							<a v-bind:href="data.href" class="my-card-link" target="_blank">LINK</a>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
    export default{
        name:'Webdesign',
        data(){
            return{
                webdesignData:[]
            }
        },
        mounted(){
        	var _this=this;
        	var url='./static/js/webdesign.data.js';
        	$.ajax({
				url:url,
				type:'GET',
				dataType:'json'
			}).done(res=>{
				_this.webdesignData=res;
			}).fail(()=>{
				console.log('fail')
			})
        }
    }
</script>
<style lang="scss" scope>
$yellow_color:#FFF100;

.my-card{
	position: relative;
	overflow: hidden;
	img{
		transition:all .5s;
	}
	.my-card-body{
		width: 100%;
		height: 100%;
		padding: 14px;
		background-color: rgba(0,0,0,.5);
		position: absolute;
		top: 0;
		left: 0;
		color:#fff;
		opacity: 0;
		transition:all .5s;
		display: flex;
		align-items:center;
		font-size: 12px;
		.my-card-link{
			background-color: $yellow_color;
			color:#000;
			padding: .1rem 1rem;
			text-decoration: none;
			display: inline-block;
			margin-top: 2px;
		}
	}
	&:hover{
		img{
			//transform:scale(1.05);
		}
		.my-card-body{
			opacity: 1;
		}
	}
}
</style>


