<template>
  <div id="app">
    <Navbar></Navbar>
    <Carousel></Carousel>
    <router-view></router-view>
    <Footers></Footers>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar';
import Carousel from "@/components/Carousel";
import Webdesign from "@/components/Webdesign";
import Footers from "@/components/Footers";

export default {
  name: "app",
  components: {
	Navbar: Navbar,
    Carousel: Carousel,
    Webdesign: Webdesign,
    Footers: Footers
  }
};
</script>

<style lang="scss">
$yellow_color: #fff100;
* {
  font-family: 微軟正黑體;
  padding: 0;
  margin: 0;
}
#app {
  background-color: #eaeaea;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  min-height: 100vh;
}
$yellow_color: #fff100;
.user-navbar {
	background-color: #333;
	border: 4px solid #fff;
	border-radius: 0;
	.navbar-brand {
		color: #fff;
	}
  .navbar-toggle {
    border: none;
    &:hover .icon-bar {
      background-color: $yellow_color;
    }
    &:focus {
      background-color: transparent;
    }
    .icon-bar {
      background-color: #fff;
    }
    &:hover {
      background-color: transparent;
    }
  }
  .navbar-nav > li > a {
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 16px;
  }
  #navbar {
    .nav {
      li {
        &:hover a {
          color: $yellow_color;
        }
        a {
          color: #fff;
        }
        &.active {
          a {
            background-color: $yellow_color;
            color: #000;
          }
        }
      }
    }
  }
}
@media (min-width: 576px) {
	.card-columns{
		column-count: 3;
	}
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) {
	.card-columns{
		column-count: 4;
	}
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
	.card-columns{
		column-count: 4;
	}
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
	.card-columns{
		column-count: 4;
	}
}
</style>



